const pedido = {
    nombreCliente: '',
    email: '',
    imagenes: []
}

document.addEventListener('DOMContentLoaded', function(){

    mostrarImagenes();

});

async function mostrarImagenes(){
    try{

        const resultado = await fetch('./imagenes.json');
        const db = await resultado.json();

        const {imagenes} = db;

        imagenes.forEach( imagen =>{
            const { id, nombre, ubicacion }= imagen;

            //DOM Script
            gallery(ubicacion, id);
        } )
    } catch(error){

        console.log('error al cargar imagenes');
    
    }
}

function gallery(url, id){
    const galeria = document.querySelector('.galeria-imagenes');

    const imagen = document.createElement('IMG');
    imagen.src = url;
    imagen.classList.add('elemento-imagen')
    //Darle un Id set en HTML 5 para la imagen
    imagen.dataset.imagenId = id;

     const lista = document.createElement('DIV');
     lista.classList.add('imagen');

    //selecciona una imagen con event handler
    lista.onclick = selectedImg;

     lista.appendChild(imagen);

     galeria.appendChild(lista);
}

function selectedImg(e){

    let img = e.target
    
    if(img.classList.contains('selected')){
        img.classList.remove('selected');
        removeImg();
    } else {
        img.classList.add('selected');
        addImg();
    }
}

function addImg(){
    console.log('se agrego img a object')
}

function removeImg(){
    console.log('se elimina imagen del object')
}